<template>
    <ul class="box">
        <li v-for="song in searchList" :key="song.id" class="common-li">
            <p class="songname">{{ song.name }}</p>
            <p class="songer">{{ getSongDetails(song) }}</p>
            <a :href="`https://y.music.163.com/m/song?id=${song.id}`">
                <i class="play"></i>
            </a>
        </li>
    </ul>
</template>

<script>
import config from '../../../config.js'
export default {
    data () {
      return {
          apiBaseUrl: config.apiBaseUrl,
          searchUrl: '/search',
          searchList: [],
      }
    },

    created() {
        this.search()
    },

    methods: {
        search() {
            console.log('//////////////////////')
            this.axios.get( `${this.apiBaseUrl+this.searchUrl}?keywords= ${this.$store.state.searchedWord}` )
            .then( res => {
                console.log('llllllllllllllll: ',res.data.result)
                this.searchList = res.data.result.songs
            })
        },
        getSongDetails(song) {
            let str = ''
            song.artists.forEach((item,index) => {
                if(index == 0)
                    str += item.name
                else
                    str += ' / ' + item.name
            })
            str += ' - ' + song.album.name
            return str
        },
    },

    components: {},
}
</script>

<style scoped>
[v-cloak] {
    display: none;
}
.box{
    padding: 10px;
}

.common-li{
    position: relative;
    width: 100%;
    height: 45px;
    border-bottom: .5px solid #eee;
}
.songer{
    font-size: 12px;
}
.play{
    position: absolute;
    display: block;
    width: 30px;
    height: 30px;
    right: 10px;
    top: 5px;
    background: url(data:image/jpeg;base64,);
}
</style>